.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: black; }

.calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 2vh;
  height: 70vh;
  width: 50vh;
  padding: 2vw; }

#display {
  display: flex;
  background-color: silver;
  height: 20%;
  align-items: flex-end;
  /* Alinea el contenido hacia la parte inferior */
  justify-content: flex-end;
  font-family: "Calculator", sans-serif;
  font-size: xx-large;
  font-weight: bold;
  overflow-x: hidden;
  white-space: nowrap; }

.calc-pad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1px;
  flex-grow: 1;
  padding: 1px;
  background-color: white; }
  .calc-pad .btn {
    border-radius: 0;
    border: none;
    color: white; }
    .calc-pad .btn.btn-primary:not(.operator) {
      background-color: black; }
    .calc-pad .btn.operator {
      background-color: #2c2c2c;
      color: white; }
    .calc-pad .btn:focus {
      box-shadow: none !important; }
    .calc-pad .btn:active {
      color: black !important;
      background-color: #ffd000 !important; }

.double-width {
  grid-column: span 2; }

.double-height {
  grid-row: span 2; }
